

.App {
  height: 100%;
}

.mainWindow{
  padding-left: 35%;
  padding-top: 5%;
}

.contect{
  padding:20%;
  font-size: 20px;
}

.title{
  font-size: 50px;
}

.box{
  border: 5px solid rgb(69, 84, 223);
  margin-left: 35%;
  justify-content: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
